@import "../gutenberg_block";

.img-content-banner{
    min-height: calc(100vh - var(--header-height));
    .img{
        .media-container{
            height: 100%;
            .vimeo-player {
                height: 100%;
                width: 100%;
            }
            img{
                width: 100%;
                height: 100%;
            }
        }
    }
    @include breakpoint(M) {
        min-height: auto;
        .content{
            padding: 3rem 3.429rem !important;
        }
    }
}
.img-content-row{
    &.layout-text-image{
        &.side-spacing{
            padding-right: 0;
            padding-left: 0;
            .img{
                width: 50%;
            }
            .content{
                padding-left: 8.125rem;
                padding-right: 8.125rem;
                h2{
                    font-weight: 700;
                }
            }
            &.wide-image{
                .img {
                    width: 60%;
                }
                .content {
                    width: 40%;
                }
            }
            @include breakpoint(M) {
                padding: 0;
                .img{
                    width: 100%;
                }
                &.wide-image{
                    .img {
                        width: 100%;
                    }
                    .content {
                        width: 100%;
                    }
                }
            }
        }
    }
    @include breakpoint(M) {
        .content{
            padding-top: 3.25rem !important;
            padding-bottom: 3.25rem !important;
            padding-left: 3.063rem !important;
            padding-right: 3.063rem !important;
        }
        &.spacing{
            padding: 0 1.75rem !important;
            .content{
                padding-top: 3.25rem !important;
                padding-bottom: 3.25rem !important;
                padding-left: 1.313rem !important;
                padding-right: 1.313rem !important;
            }
        }
    }
    &.layout-image-text{
        &.side-spacing{
            padding-right: 0;
            padding-left: 0;
            .img{
                width: calc(50%);
            }
            .content{
                padding-left: 8.125rem;
                padding-right: 8.125rem;
                h2{
                    font-weight: 700;
                }
            }
            &.wide-image{
                .img {
                    width: 60%;
                }
                .content {
                    width: 40%;
                }
            }
            @include breakpoint(M) {
                padding: 0;
                .img{
                    width: 100%;
                }
                &.wide-image{
                    .img {
                        width: 100%;
                    }
                    .content {
                        width: 100%;
                    }
                }
            }
        }
    }
    &.wide-image{
        .img {
            width: 60%;
        }
        .content {
            width: 40%;
        }
        @include breakpoint(M) {
            padding: 0 !important;
            .img {
                width: 100%;
            }
            .content {
                width: 100%;
                padding-left: 3.063rem !important;
                padding-right: 3.063rem !important;
            }
        }
    }
}
.img-content {
    display: flex;
    position: relative;

    @include breakpoint(M) {
        flex-direction: column;
    }
    &.slick-slide {
        display: flex !important;
    }
    .content {
        width: 50%;
        padding: 0 0 0 3.714rem;
        flex-grow: 1;
        flex-basis: 0;
        display: flex;
        justify-content: center;
        align-items: center;
        padding-top: 2rem;
        padding-bottom: 2rem;

        @include breakpoint(M) {
            padding: 0 0 0 3rem;
        }
        @include breakpoint(M) {
            padding: 2rem 0 0 0;
            margin: 0;
            width: 100%;
            text-align: center;
            flex-basis: auto;

            .buttons {
                justify-content: center;
                margin-bottom: 0;
                width: 100%;
                
                .button {
                    white-space: nowrap;
                }
                @include breakpoint(M) {
                    justify-content: start;
                }
            }
            &.xs\:text-left {
                text-align: left;
                
                .content-inner {
                    align-items: flex-start;
                }
                .buttons {
                    justify-content: flex-start;
                }
            }
        }
        .content-inner {
            width: 100%;
            max-width: 32rem;
            display: flex;
            flex-direction: column;
            margin: 0 auto;

            @include breakpoint(M) {
                align-items: start;
                text-align: left;
                &.mobile-left{
                    align-items: start;
                    text-align: left;
                }
                &.mobile-centre{
                    align-items: center;
                    text-align: center;
                    .buttons{
                        justify-content: center;
                    }
                }
                &.mobile-right{
                    align-items: end;
                    text-align: right;
                    .buttons{
                        justify-content: end;
                    }
                }
            }
        }
        .item{
            .subtitle-2{
                letter-spacing: 0.15em;
            }
        }
        .content-wrap {
            width: 100%;
            font-family: $font-stack-headings;
            p, ul li{
                font-family: $font-stack-headings;
            }
            @include breakpoint(M) {
                p{
                    @include size-M;
                }
            }
            &.content-wrap-careers{
                p{
                    @include size-S;
                    margin-bottom: 0;
                }
                ul li{
                    font-family: $font-stack-headings;
                    @include size-S;
                    margin-bottom: 0;
                }
            }
        }
    }
    .img {
        width: 50%;
        position: relative;

        .image-carousel {
           // min-height: 30rem;

            @include breakpoint(M) {
              //  min-height: 16rem;
            }
            &.shorter {
              //  min-height: 24rem;

                @include breakpoint(M) {
                   // min-height: 16rem;
                }
            }
        }
        @include breakpoint(M) {
            width: 100%;
        }
        img {
         //   position: absolute;
         //   top: 0;
         //   left: 0;
         //   height: 100%;
            width: 100%;
        }
        .slick-list,
        .slick-track {
         //   position: absolute;
            top: 0;
            left: 0;
            height: 100%;
            width: 100%;
        }
        .slick-controls {
            width: 100%;

            .slick-control {
                @include color(color, 'body');

                &:hover {
                    @include color(color, 'accent-primary');
                }
            }
        }
    }
    &.text-image {
        .content {
            order: 1;
            padding: 0 3.714rem 0 0;
            padding-top: 2rem;
            padding-bottom: 2rem;

            @include breakpoint(M) {
                padding: 0 3rem 0 0;
            }
            @include breakpoint(M) {
                padding: 2rem 0 0 0;
                display: block;
                margin: 0;
                order: unset;
            }
        }
        .img {
            order: 2;

            @include breakpoint(M) {
                order: unset;
                width: 100%;
            }
        }
    }
    &.mob-img-bottom {
        @include breakpoint(M) {
            .content {
                order: 1;
                padding: 0 0 2rem 0;
            }
            .img {
                order: 2;
            }
        }
    }
    &.column {
        flex-direction: column;

        .content {
            padding: 2rem 0 0 0;
            margin: 0;
            width: 100%;
            text-align: center;
            flex-basis: auto;
            align-items: unset;

            .buttons {
                justify-content: center;
                margin-bottom: 0;
                @include breakpoint(M) {
                    justify-content: start;
                }
            }
        }
        .img {
            width: 100%;
            
            .image-carousel {
                min-height: 18rem;

                @include breakpoint(M) {
                    min-height: 16rem;
                }
            }
        }
    }
}

.button.icon.content-carousel-prev,
.button.icon.content-carousel-next {
    position: absolute;
    right: 2rem;
    margin: 0;

    @include breakpoint(M) {
        right: 1rem;
    }
}

.button.icon.content-carousel-prev {
    top: calc(50% - 3.25rem);

    @include breakpoint(M) {
        left: calc(50% - 3.25rem);
        top: auto;
        bottom: 2rem;
    }
}

.button.icon.content-carousel-next {
    bottom: calc(50% - 3.25rem);

    @include breakpoint(M) {
        right: calc(50% - 3.25rem);
        top: auto;
        bottom: 2rem;
    }
}

.content-carousel-container {
    @include breakpoint(M) {
        padding-bottom: 7rem;
    }
}

.img-content-columns {
    padding-left: 5rem;
    padding-right: 5rem;

    @include breakpoint(ML) {
        padding-left: 4rem;
        padding-right: 4rem;
    }
    @include breakpoint(M) {
        padding-left: 0;
        padding-right: 0;
    }
    .img-content {
        margin: 0 1rem;
        width: calc(50% - 2rem - 1px);

        @include breakpoint(M) {
            margin: 0;
            width: 100%;
            padding-bottom: 2rem;
        }
    }
    &.flex-wrap {
        .img-content {
            margin-bottom: 5.5rem;
        }
    }
}
.image-fill-content{
    .img{
        overflow: hidden;
    }
    .media-container, img, video{
        height: 100%;
        aspect-ratio: 1/1;
    }
}

.content-text-small{
    *{
        @include size-S;
    }
}

.content-style-list{
    .subtitle-2{
        font-style: normal;
        margin-top: 2rem;
        @include size-L;
    }
    .content-wrap{
        p{
            text-transform: uppercase;
        }
    }
}

.img-content{
    .buttons{
        .button.primary:last-child{
            margin-top: 3rem;
        }
    }
}

//Addional styling for map style block
section.map-style-block{
    .img-content{
        .content{
            padding-top: 0;
            padding-bottom: 0;
            width: 30%;
            @include breakpoint(L){
                width: 40%;
            }
            @include breakpoint(M){
                width: 100%;
            }
        }
        .img{
            width: 70%;
            @include breakpoint(L){
                width: 60%;
            }
            @include breakpoint(M){
                width: 100%;
            }
            .media-container{
                height: 100%;
                img{
                    width: 100%;
                    height: 100%;
                    object-fit: contain;
                }
            }
        }
        &.text-image{
            .content{
                padding-left: 5.5rem;
                @include breakpoint(M){
                    padding-left: 0;
                }
            }
        }
        &.image-text{
            .content{
                padding-right: 5.5rem;
                @include breakpoint(M){
                    padding-right: 0;
                }
            }
        }
    }
}